.header {
  background: #1C2B35;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 5%;
  padding-right: 5%;
}

.header a {
  text-decoration: none;
  color: #fff;
  font-size: 17px;
  margin-left: 36px;
}

.header a:hover {
  color: orange;
}